import React, { Component } from 'react';
import Device from './Device';
import Button from './Button';
import nextId from "react-id-generator";
import {scroller} from "react-scroll";
import strings from "../../Localisation";
import {sendAmplitudeData} from "../../../utils/Amplitude";
import FloatingButton from "./FloatingButton";

export class Presentation extends Component {

    constructor(props) {
        super(props);
        this.buttonId = nextId();
        this.smallButtonId = nextId();
        this.isSmallButtonRendered = false;
    }

    componentDidMount() {
        let button = document.getElementById(this.buttonId);
        let smallButton = document.getElementById(this.smallButtonId);
        let link = this;

        button.addEventListener('click', function(event){
            event.preventDefault();

            link.swipeDown();
        });

        smallButton.addEventListener('click', function(event){
            event.preventDefault();

            link.swipeDown();
        });

        window.addEventListener("scroll", function(event) {
            if (window.pageYOffset > button.offsetTop) {
                if (!link.isSmallButtonRendered) {
                    link.isSmallButtonRendered = true;
                    link.forceUpdate();
                }
            }

            else {
                if (link.isSmallButtonRendered) {
                    link.isSmallButtonRendered = false;
                    link.forceUpdate();
                }
            }
        });
    }

    swipeDown(){
        sendAmplitudeData("Presentation: swiping down");

        scroller.scrollTo('footer_space', {
            duration: 1500,
            delay: 100,
            smooth: true,
            offset: 50
        })
    }

    render() {
        return (
            <div>
                <div className={"container_up_space"}/>

                <div className={"presentation"}>
                    <Device src={"/images/main/presentation/reface.png"}/>
                    <Button id={this.buttonId} text={strings.presentation__button_preorder}/>
                </div>

                <FloatingButton isVisible={this.isSmallButtonRendered} id={this.smallButtonId} text={strings.presentation__button_preorder}/>
            </div>
        )
    }
}