import React, { Component } from 'react';
let classNames = require('classnames');

export default class FloatingButton extends Component {

    render() {
        let className = classNames("presentation__floating_button", {
            "presentation__floating_button__visible": this.props.isVisible
        });

        return (
            <div className={className} id={this.props.id}>
                <div className={"presentation__floating_button_text"}>{this.props.text}</div>
            </div>
        )
    }
}